.review-item{max-width:525px;width:100%;min-height:310px;border:1px solid #fcd54c;padding:70px 30px 30px 30px;margin-top:56px;position:relative}@media screen and (max-width: 1024px){.review-item{min-height:250px}}@media screen and (max-width: 760px){.review-item{max-width:inherit}}.review-item::after{position:absolute;content:"”";right:30px;top:-10px;font-weight:600;font-size:120px;line-height:200px;font-family:"Work Sans",sans-serif;color:#fcd54c}.review-item__img{position:absolute;left:30px;top:-56px;width:112px;height:112px}.review-item__name{font-size:25px;line-height:30px;font-weight:700;margin-bottom:20px}.review-item__position{font-size:16px;line-height:19px;font-family:"Work Sans",sans-serif;margin-bottom:35px}.review-item__company{color:#fcd54c}.review-item__text{font-size:16px;line-height:26px;font-family:"Work Sans",sans-serif;color:#929292}@media screen and (max-width: 1024px){.review-item__text{line-height:14px;font-size:12px}}